<template>
  <div>
    <div>
      <b-modal id="modal-sm" size="sm" no-close-on-backdrop title="Confirm">
        <p class="my-2">Are you sure to raise the internal stock request?</p>
        <template v-slot:modal-footer="{ cancel }">
          <b-button
            size="sm"
            class="float-right mb-3 p-2 pl-5 pr-5"
            variant="outline-primary"
            @click="cancel()"
          >Cancel</b-button>
          <b-button
            size="sm"
            class="float-right mb-3 p-2 pl-5 pr-5"
            variant="primary"
            @click="saveInternalStockRequest"
          >Confirm</b-button>
        </template>
      </b-modal>
    </div>
    <div class="d-block header-section">
      <div class="py-3 px-3 align-self-center d-sm-flex w-100 rounded">
        <div class="w-sm-100 mr-auto">
          <h1>
            <b>Stock Transfer Request</b>
          </h1>
        </div>
      </div>
    </div>
    <div class="container-fluid productManagement">
      <!-- START: Card Data-->
      <div class="row">
        <div class="col-12 col-md-12">
          <div class="card">
            <div class="card-header p-0">
              <div class="col-12 col-sm-11 ml-5 mr-4 mt-5">
                <div class="form-row ml-3 mb-4">
                  <div class="form-group row col-md-6">
                    <label for="username" class="col-sm-3 col-form-label pr-0 pl-0">Product</label>
                    <div class="col-sm-8">
                      <select class="form-control" v-model="selectedProduct">
                        <option disabled selected value>-select-</option>
                        <option
                          v-for="item in products"
                          :value="{ id: item.productId, text: item.productName }"
                          :key="item.productId"
                        >{{ item.productName }}</option>
                      </select>
                    </div>
                  </div>
                  <div class="form-group row col-md-6">
                    <label
                      for="username"
                      class="col-sm-3 col-form-label pr-0 pl-0"
                    >No of Stamps Required</label>
                    <div class="col-sm-8">
                      <input
                        v-model.number="noOfStamps"
                        type="number"
                        max="100000"
                        min="1000"
                        class="form-control"
                        placeholder
                      />
                      <div class="invalid-feedback"></div>
                    </div>
                  </div>
                </div>
                <div class="form-row ml-3 mb-4">
                  <div class="form-group row col-md-6">
                    <label
                      for="username"
                      class="col-sm-3 col-form-label pr-0 pl-0"
                    >Requesting Facility</label>
                    {{this.requestingFacilityName}}
                  </div>
                </div>

                <div class="form-row ml-3">
                  <div class="form-group row col-md-6">
                    <label for="username" class="col-sm-3 col-form-label pr-0 pl-0">Notes</label>
                    <div class="col-sm-8">
                      <textarea v-model.trim="notes" class="form-control" placeholder></textarea>
                    </div>
                  </div>
                </div>                
                <b-button
                  v-b-modal.modal-sm
                  class="float-right mt-3 mb-3 p-2 pl-5 pr-5"
                  variant="primary"
                  :disabled="!selectedProduct || !noOfStamps || !notes"
                >SUBMIT</b-button>
                <b-button
                  v-b-modal.modal-sm
                  class="float-right mt-3 mb-3 mr-2 p-2 pl-5 pr-5"
                  variant="outline-primary"
                  @click="redirectToStockRequests"
                >CANCEL</b-button>
              </div>
              <div class="clearfix"></div>
            </div>
          </div>
        </div>
      </div>
      <!-- END: Card DATA-->
    </div>
  </div>
</template>

<script>
import axios from "axios";
export default {
  data() {
    return {
      products: [],
      selectedProduct: "",
      requestingFacilityId: null,
      requestingFacilityName: "",
      noOfStamps: 1000,
      notes: "",
    };
  },
  created() {
    this.requestingFacilityId = this.$store.state.clientAppDecodedToken.LocationId;
    this.requestingFacilityName = this.$store.state.clientAppDecodedToken.LocationName;
    this.getProducts();
  },
  components: {},

  methods: {
    getProducts() {
      axios
        .get("/Meta/GetProducts", {
          baseURL: process.env.VUE_APP_OrderMgmtApisBaseUrl
        })
        .then((response) => {
          var tempProductType = [];
          for (let key in response.data) {
            tempProductType.push({
              productId: key,
              productName: response.data[key],
            });
          }
          this.products = tempProductType;
        });
    },
    saveInternalStockRequest() {
		var reqBody ={productId: parseInt(this.selectedProduct.id),noOfStamps: this.noOfStamps, notes:this.notes}
      axios
        .post("/InternalStock", reqBody, {
          baseURL: process.env.VUE_APP_OrderMgmtApisBaseUrl,
        })
        .then(() => {
          this.$router.push({ name: "StockRequestList" });
        });
    },
    redirectToStockRequests() {
      this.$router.push({ name: "StockRequestList" });
    },
  },
};
</script>
